@import "/node_modules/normalize.css/normalize.css";
@font-face {
  font-family: "SDGR";
  src: url("../font/AppleSDGothicNeoR.ttf");
  font-family: "SDGB";
  src: url("../font/AppleSDGothicNeoB.ttf");
}
html,
.root__cont {
  width: 100%;
  height: 100%;
}
.gnb__cont {
  width: 100%;
  position: absolute;
  top: 0;
  z-index: 9;
  background-color: #151f2e85;
  transition: all 0.4s ease-in-out;
  font-family: SDGR;
  .inner_cont {
    min-width: 1300px;
    max-width: 100%;
    margin: 0 auto;

    .main_menus {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #ccc;
      color: white;
      font-family: SDGB;
      .left {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        margin-left: 30px;
        height: 100px;
        width: 10%;
        & > a > img {
          width: 100px;
        }
      }
      .center {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        margin-right: 50px;
        width: 50%;
        align-items: center;

        & > div {
          height: 100px;
          line-height: 100px;
          width: 100px;
          text-align: center;
          margin-left: 20px;
          position: relative;

          & > div::before {
            position: absolute;
            transition: width 0.3s ease-in-out;
            left: 50%;
            transform: translateX(-50%);
            background: #e60012;
            opacity: 1;
            content: "";
            width: 0;
            top: 6rem;
            height: 5px;
          }

          & > .active::before {
            width: 6rem;
          }

          & > div {
            & > a {
              text-decoration: none;
              color: white;
            }
          }
        }
      }
      .right {
        margin-left: 50px;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        width: 20%;
        height: 80px;
        margin-right: 35px;
        .btn-cont {
          display: flex;
          width: 130px;
          justify-content: space-between;
          & > .btn {
            border: 1px solid white;
            font-size: 15px;
            padding: 3px;
          }
        }
        .lang {
          display: flex;
          #lang_text {
            line-height: 30px;
            margin: 0px 10px;
          }
          div > img {
            width: 30px;
          }
        }
        .ham {
          display: flex;
          & > img {
            width: 30px;
          }
        }
      }
    }
    .sub_menus {
      border-bottom: 1px solid #ccc;
      flex-direction: row;
      justify-content: space-between;
      display: flex;
      visibility: none;
      color: transparent;
      height: 0px;
      transition: height 0.3s ease-in-out, color 0.3s,
        display 1s ease-in-out 0.2s;
      .left {
        width: 10%;
      }
      .center {
        display: none;
        flex-direction: row;
        justify-content: space-around;
        margin-right: 50px;
        width: 50%;
        align-items: center;
        & > :first-child {
          position: relative;
          left: 1px;
        }
        & > div {
          width: 160px;
          margin-left: 2px;
          & > ul {
            list-style: none;
            padding: 0px;
            & > li {
              font-size: 13px;
              margin: 10px 0px;
              text-align: center;
              color: #222222;
              & > a {
                color: transparent;
                text-decoration: none;
                transition: all 0.3s ease-in-out;
              }
            }
          }
        }
      }
      .right {
        width: 20%;
        margin-right: 35px;
      }
    }
  }
}

.svg-icon {
  filter: invert(1);
}

.your-class {
  img {
    height: 100vh;
    width: 100%;
    object-fit: cover;
  }
}

.control_wrap {
  position: absolute;
  bottom: 7rem;
  left: 50%;
  transform: translateX(-50%);
  width: 150px;
}
.control_wrap .slick-dots {
  overflow: hidden;
  float: left;
}
.control_wrap .slick-dots li {
  float: left;
}
.control_wrap .play_wrap {
  float: right;
}
.control_wrap .play_wrap {
  width: 0.7rem;
  height: 0.7rem;
  position: relative;
}
.control_wrap .play_wrap button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.control_wrap .play_wrap button i {
  color: #fff;
}
.control_wrap .play_wrap button.stop {
  display: none;
}
.control_wrap .play_wrap button.play {
  display: none;
}
.control_wrap .play_wrap button.stop.on {
  display: block;
}
.control_wrap .play_wrap button.play.on {
  display: block;
}

.control_wrap .slick-dots li button {
  width: 0.7rem;
  height: 0.7rem;
  display: block;
  font-size: 0;
  border-radius: 50%;
  border: 1px solid #fff;
}
.control_wrap .slick-dots li {
  margin-right: 1.45rem;
}
.control_wrap .slick-dots li.slick-active button {
  background: #fff;
  transition: all 0.5s;
}

.footer {
  width: 100%;
  background-color: #414141;
  height: 140px;

  .info {
    border-bottom: 1px solid #ccc;
    height: 70px;
    .container {
      height: 70px;
      display: flex;
      justify-content: space-between;
      line-height: 70px;
      color: white;
      .menu {
        display: flex;
        flex-direction: row;
        & div {
          font-size: 13px;
        }
        & div:nth-child(2) > span {
          border-left: 1px solid #ccc;
          border-right: 1px solid #ccc;
          padding: 0 20px;
        }
        & div + div {
          margin-left: 20px;
        }
      }
      .icon {
        display: flex;
        flex-direction: row;
        align-items: center;
        & > img {
          width: 40px;
          height: 40px;
          margin-left: 10px;
        }
      }
    }
  }
  .basic {
    .container {
      height: 70px;
      & > img {
        width: 60px;
        margin-left: -10px;
      }
    }
  }
}

.contact_us_cont {
  position: absolute;
  bottom: 80px;
  right: 80px;
  .contact_inner {
    width: 120px;
    height: 120px;
    background-color: #007c91;
    color: white;
    border-radius: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .sub_title {
      font-size: 8px;
    }
    .main_title {
      font-size: 20px;
    }
  }
}
